
import React, { useState, useCallback, useRef, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from '@/components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Badge } from '@/components/ui/badge';
import { useToast } from '@/hooks/use-toast';
import { Upload, Save, X, Edit, Edit3, File, FileText, Plus, Download, FolderOpen, Trash2, RefreshCw, AlertTriangle } from 'lucide-react';
import { Progress } from '@/components/ui/progress';
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import EmergencyFlowEditor from './emergency-flow-editor';

interface FlowFile {
  id: string;
  title: string;
  description: string;
  fileName: string;
  createdAt: string;
  trigger?: string[];
  slides?: any[];
}

interface FlowData {
  id: string;
  title: string;
  description: string;
  triggerKeywords: string[];
  steps: any[];
  updatedAt?: string;
}

const EmergencyFlowCreator: React.FC = () => {
  const { toast } = useToast();
  const fileInputRef = useRef<HTMLInputElement>(null);
  
  // 
  const [activeTab, setActiveTab] = useState<'new' | 'upload' | 'edit'>('new');
  const [flowList, setFlowList] = useState<FlowFile[]>([]);
  const [isLoadingFlowList, setIsLoadingFlowList] = useState(false);
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [isUploading, setIsUploading] = useState(false);
  const [uploadProgress, setUploadProgress] = useState(0);
  const [uploadSuccess, setUploadSuccess] = useState(false);
  const [uploadedFileName, setUploadedFileName] = useState('');
  const [selectedFlowForEdit, setSelectedFlowForEdit] = useState<string | null>(null);
  const [currentFlowData, setCurrentFlowData] = useState<FlowData | null>(null);
  
  // 
  const [showDeleteDialog, setShowDeleteDialog] = useState(false);
  const [flowToDelete, setFlowToDelete] = useState<FlowFile | null>(null);
  const [isDeleting, setIsDeleting] = useState(false);

  // 
  const fetchFlowList = useCallback(async (forceRefresh = false) => {
    try {
      setIsLoadingFlowList(true);
      console.log(`  (forceRefresh: ${forceRefresh})`);

      // 
      const timestamp = Date.now();
      const cacheParams = forceRefresh ? 
        `?_t=${timestamp}&_r=${Math.random().toString(36).substring(2)}&force_refresh=true` : 
        `?_t=${timestamp}`;

      const response = await fetch(`/api/emergency-flow/list${cacheParams}`, {
        method: 'GET',
        headers: {
          'Cache-Control': 'no-cache, no-store, must-revalidate, max-age=0',
          'Pragma': 'no-cache',
          'Expires': 'Thu, 01 Jan 1970 00:00:00 GMT',
          'X-Requested-With': 'XMLHttpRequest',
          'X-Force-Refresh': forceRefresh ? 'true' : 'false'
        }
      });

      if (!response.ok) {
        throw new Error(`: ${response.status}`);
      }

      const data = await response.json();
      console.log(` :`, data);

      // 
      const validData = Array.isArray(data) ? data : [];
      
      // 
      const uniqueData = validData.filter((item, index, arr) => 
        arr.findIndex(t => t.id === item.id) === index
      );

      // 
      const currentIds = new Set(uniqueData.map(item => item.id));
      const removedItems = flowList.filter(item => !currentIds.has(item.id));
      
      if (removedItems.length > 0) {
        console.log(` :`, removedItems);
        removedItems.forEach(item => {
          toast({
            title: "",
            description: `${item.title}`,
            variant: "default"
          });
        });
      }

      setFlowList(uniqueData);

      // 
      window.dispatchEvent(new CustomEvent('forceRefreshFlowList', {
        detail: { flowList: uniqueData }
      }));

    } catch (error) {
      console.error(' :', error);
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
      setFlowList([]);
    } finally {
      setIsLoadingFlowList(false);
    }
  }, [flowList, toast]);

  // 
  useEffect(() => {
    fetchFlowList(true);

    // 
    const handleDataUpdate = () => {
      console.log('  - ');
      fetchFlowList(true);
    };

    const eventTypes = [
      'flowDataUpdated',
      'troubleshootingDataUpdated',
      'emergencyFlowSaved',
      'fileSystemUpdated',
      'forceRefreshFlowList'
    ];

    eventTypes.forEach(eventType => {
      window.addEventListener(eventType, handleDataUpdate);
    });

    // 
    const intervalId = setInterval(() => {
      fetchFlowList(true);
    }, 30000); // 30

    return () => {
      eventTypes.forEach(eventType => {
        window.removeEventListener(eventType, handleDataUpdate);
      });
      clearInterval(intervalId);
    };
  }, []);

  // 
  const handleFileSelect = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      setSelectedFile(file);
      setUploadSuccess(false);
      setUploadedFileName('');
    }
  };

  // 
  const handleUpload = async () => {
    if (!selectedFile) {
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
      return;
    }

    const formData = new FormData();
    formData.append('file', selectedFile);

    setIsUploading(true);
    setUploadProgress(0);

    try {
      // 
      const progressInterval = setInterval(() => {
        setUploadProgress(prev => Math.min(prev + 10, 90));
      }, 100);

      const response = await fetch('/api/emergency-flow/upload', {
        method: 'POST',
        body: formData
      });

      clearInterval(progressInterval);
      setUploadProgress(100);

      if (!response.ok) {
        throw new Error('');
      }

      const result = await response.json();
      
      setUploadSuccess(true);
      setUploadedFileName(selectedFile.name);
      
      toast({
        title: "",
        description: `${selectedFile.name} `,
      });

      // 
      await fetchFlowList(true);
      
      // 
      setActiveTab('edit');

    } catch (error) {
      console.error(':', error);
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
    } finally {
      setIsUploading(false);
      setUploadProgress(0);
      setSelectedFile(null);
      if (fileInputRef.current) {
        fileInputRef.current.value = '';
      }
    }
  };

  // 
  const loadFlowForEdit = async (flowId: string) => {
    try {
      const response = await fetch(`/api/emergency-flow/${flowId}?_t=${Date.now()}`, {
        headers: {
          'Cache-Control': 'no-cache'
        }
      });
      
      if (!response.ok) {
        throw new Error('');
      }
      
      const data = await response.json();
      setCurrentFlowData(data);
      setSelectedFlowForEdit(flowId);
      
    } catch (error) {
      console.error(':', error);
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
    }
  };

  // 
  const deleteFlow = async (flowId: string) => {
    setIsDeleting(true);
    try {
      const response = await fetch(`/api/emergency-flow/${flowId}`, {
        method: 'DELETE'
      });

      if (!response.ok) {
        throw new Error('');
      }

      toast({
        title: "",
        description: "",
      });

      // 
      if (selectedFlowForEdit === flowId) {
        setSelectedFlowForEdit(null);
        setCurrentFlowData(null);
      }

      // 
      await fetchFlowList(true);

    } catch (error) {
      console.error(':', error);
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
    } finally {
      setIsDeleting(false);
      setShowDeleteDialog(false);
      setFlowToDelete(null);
    }
  };

  // 
  const handleFlowSave = (savedData: FlowData) => {
    setCurrentFlowData(savedData);
    fetchFlowList(true);
  };

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <h2 className="text-xl font-bold"></h2>
        <Button onClick={() => fetchFlowList(true)} variant="outline" size="sm">
          <RefreshCw className="w-4 h-4 mr-2" />
          
        </Button>
      </div>

      <Tabs value={activeTab} onValueChange={setActiveTab}>
        <TabsList className="grid w-full grid-cols-3">
          <TabsTrigger value="new"></TabsTrigger>
          <TabsTrigger value="upload"></TabsTrigger>
          <TabsTrigger value="edit" disabled={!flowList.length}></TabsTrigger>
        </TabsList>

        <TabsContent value="new" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Plus className="w-5 h-5" />
                
              </CardTitle>
              <CardDescription>
                
              </CardDescription>
            </CardHeader>
            <CardContent>
              <EmergencyFlowEditor flowData={null} onSave={handleFlowSave} />
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="upload" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Upload className="w-5 h-5" />
                
              </CardTitle>
              <CardDescription>
                JSON
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="border-2 border-dashed border-gray-300 rounded-lg p-6 text-center">
                <input
                  ref={fileInputRef}
                  type="file"
                  accept=".json"
                  onChange={handleFileSelect}
                  className="hidden"
                />
                {selectedFile ? (
                  <div className="space-y-2">
                    <FileText className="mx-auto h-8 w-8 text-blue-500" />
                    <p className="text-sm font-medium">{selectedFile.name}</p>
                    <p className="text-xs text-gray-500">
                      {(selectedFile.size / 1024).toFixed(1)} KB
                    </p>
                  </div>
                ) : (
                  <div className="space-y-2">
                    <FolderOpen className="mx-auto h-8 w-8 text-gray-400" />
                    <p className="text-sm text-gray-500">JSON</p>
                  </div>
                )}
                <Button
                  variant="outline"
                  onClick={() => fileInputRef.current?.click()}
                  className="mt-2"
                >
                  
                </Button>
              </div>

              {isUploading && (
                <div className="space-y-2">
                  <Progress value={uploadProgress} />
                  <p className="text-sm text-center">... {uploadProgress}%</p>
                </div>
              )}

              <div className="flex gap-2">
                <Button
                  onClick={handleUpload}
                  disabled={!selectedFile || isUploading}
                  className="flex-1"
                >
                  <Upload className="w-4 h-4 mr-2" />
                  {isUploading ? '...' : ''}
                </Button>
                {selectedFile && (
                  <Button
                    variant="outline"
                    onClick={() => {
                      setSelectedFile(null);
                      if (fileInputRef.current) {
                        fileInputRef.current.value = '';
                      }
                    }}
                  >
                    <X className="w-4 h-4" />
                  </Button>
                )}
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="edit" className="space-y-4">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/*  */}
            <Card>
              <CardHeader>
                <CardTitle></CardTitle>
                <CardDescription>
                   ({flowList.length})
                </CardDescription>
              </CardHeader>
              <CardContent>
                {isLoadingFlowList ? (
                  <div className="text-center py-4">
                    <p className="text-sm text-gray-500">...</p>
                  </div>
                ) : flowList.length === 0 ? (
                  <div className="text-center py-4">
                    <p className="text-sm text-gray-500"></p>
                  </div>
                ) : (
                  <div className="space-y-2 max-h-96 overflow-y-auto">
                    {flowList.map((flow) => (
                      <div
                        key={flow.id}
                        className={`p-3 border rounded-lg cursor-pointer transition-colors ${
                          selectedFlowForEdit === flow.id
                            ? 'border-blue-500 bg-blue-50'
                            : 'border-gray-200 hover:border-gray-300'
                        }`}
                      >
                        <div className="flex items-center justify-between">
                          <div
                            className="flex-1"
                            onClick={() => loadFlowForEdit(flow.id)}
                          >
                            <h4 className="font-medium text-sm">{flow.title}</h4>
                            <p className="text-xs text-gray-500 mt-1">
                              {flow.description}
                            </p>
                            <div className="flex items-center gap-2 mt-2">
                              <Badge variant="outline" className="text-xs">
                                {flow.fileName}
                              </Badge>
                            </div>
                          </div>
                          <Button
                            size="sm"
                            variant="ghost"
                            onClick={(e) => {
                              e.stopPropagation();
                              setFlowToDelete(flow);
                              setShowDeleteDialog(true);
                            }}
                          >
                            <Trash2 className="w-4 h-4" />
                          </Button>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>

            {/*  */}
            <Card>
              <CardHeader>
                <CardTitle></CardTitle>
              </CardHeader>
              <CardContent>
                {selectedFlowForEdit && currentFlowData ? (
                  <EmergencyFlowEditor
                    flowData={currentFlowData}
                    onSave={handleFlowSave}
                  />
                ) : (
                  <div className="text-center py-8">
                    <p className="text-gray-500"></p>
                  </div>
                )}
              </CardContent>
            </Card>
          </div>
        </TabsContent>
      </Tabs>

      {/*  */}
      <AlertDialog open={showDeleteDialog} onOpenChange={setShowDeleteDialog}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle></AlertDialogTitle>
            <AlertDialogDescription>
              {flowToDelete?.title}
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel></AlertDialogCancel>
            <AlertDialogAction
              onClick={() => flowToDelete && deleteFlow(flowToDelete.id)}
              disabled={isDeleting}
            >
              {isDeleting ? '...' : ''}
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </div>
  );
};

export default EmergencyFlowCreator;
